import React from 'react'
import './Boost.css'
import PanelInfo from "./PanelInfo/PanelInfo";
import Button from "../../common/Button/Button";

class Boost extends React.Component {

    initialState=[
        {ava:'https://i.ibb.co/N30qqwR/palec18.jpg', text: '  -    -  2', id: 1,
        vk:'https://vk.com/pricell_art'},
        {ava:'https://i.ibb.co/YXcmCFv/DimaAMDa.jpg', text: '  -    Apex, GTA5', id: 2,
        vk:'https://vk.com/im?sel=144402424'},
        {ava:'https://i.ibb.co/tqwt7yQ/Alexey.jpg', text: '  -  ,  ', id: 3,
        vk:'https://vk.com/milinkiewicz'},
        {ava:'https://i.ibb.co/M9jnvrR/slava.jpg', text: '  -   , ', id: 4,
        vk:'https://vk.com/slavalobikov'},

    ];




    render() {

        let panel = this.initialState.map ( p => <PanelInfo ava={p.ava} key={p.id} text={p.text}  vk={p.vk} /> );

        return (
            <div className='boost'>

                <div>
                    <div className='Zakaz'> -   !</div>
                    <div><img className='boostImg' src="https://i.ibb.co/PmHLrpV/komanda.jpg" alt=""/></div>
                    <div className='boostGrid'>
                        {panel}
                        </div>
                        <PanelInfo />
                    </div>
                    <Button a='https://discordapp.com/invite/S6Qwxp' text=' -'/>
                    <div className='had'></div>
                </div>


        )
    }
}

export default Boost;


